
import React from 'react';
import axios from 'axios';

export default class PersonAdd extends React.Component {
    state = {
        id: null,
        fname:'',
        lname:'',
        age:0
    }

    handleChangefname = event => {
        this.setState({
            fname: event.target.value
        });
    }
    handleChangelname = event => {
        this.setState({
            lname: event.target.value,
        });
    }
    handleChangeage = event => {
        this.setState({
            age: event.target.value
        });
    }

    handleSubmit = event => {
        event.preventDefault();

        const person = {
            id: null,
            fname: this.state.fname,
            lname: this.state.lname,
            age: this.state.age

        };
        axios.post(`http://localhost:8080/person`, {fname:this.state.fname,lname:this.state.lname,age:this.state.age})
            .then(res => {
                console.log(res);
                console.log(res.data);
            })
    }

    render() {
        return (
            <div>
                <form onSubmit={this.handleSubmit}>
                    <label>
                        fname:
                        <input type="text" fname="fname" onChange={this.handleChangefname} />
                    </label>
                    <label>
                        lname:
                        <input type="text" lname="lname" onChange={this.handleChangelname} />
                    </label>
                    <label>
                        age:
                        <input type="int" age="age" onChange={this.handleChangeage} />
                    </label>
                    <button type="submit">Add</button>
                </form>
            </div>
        )
    }
}